.root {
  display: flex;
}

.PanelHeader-content--alternate, .PanelHeader-left-in--alternate, .PanelHeader-addon--alternate, .PanelHeader-right--alternate, .PanelHeader-bg--alternate {
  color: #5181b8;
}


.time-wrapper {
  display: flex;
  flex-direction: column;
  align-content: space-between;
  padding-right: 10px;
  border-right: 2px solid;
}

.examStatus {
  color: #909499;
  line-height: 20px;
}

.content-wrapper {
  display: flex;
  flex-direction: row;
  align-content: space-between;
}

.info-wrapper {
  padding-left: 10px;
}

.sharing-wrapper {
  position: absolute;
  right: 0px;
}

.time-day {
  align-self: flex-start;
  color: #000000;
}

.time-month {
  align-self: flex-start;
  color: #909499;
}

.time-year {
  align-self: flex-start;
  color: #909499;
}

.lesson {
  letter-spacing: 0.1px;
  font-weight: bold;
  line-height: 20px;
}

.testScore {
  line-height: 20px;
}


.DonePopover .PopoutWrapper__mask {
  background: none;
}

.Link:active {
  color: #40638c;
}

.DonePopover {
  animation-duration: 1.5s;
  animation-name: done-popover-intro;
}

.DonePopover__container {
  width: 160px;
  height: 80px;
  background: var(--tabbar_background);
  box-shadow: 0 16px 32px 0 rgba(0, 0, 0, .08), 0 0 8px 0 rgba(0, 0, 0, .08);
  border-radius: 8px;
}

.DonePopover__container .DonePopover {
  color: var(--overlay_status_icon);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1px;
}

.DoneIcon {
  top: 3px
}

.DoneText {
  font-size: 11pt;
  text-align: center;
}

@keyframes done-popover-intro {
  from {
    opacity: 0;
  }

  to {
    opacity: 0;
  }

  30% {
    opacity: 1;
  }
  70% {
    opacity: 1;
  }
}

.Cell--ios .Cell__in {
  padding: 0;
}

.Cell--android .Cell__in {
  padding: 0;
}

.TopCellBefore {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 6px;
}

.TopCellBeforePlace {
  width: 53px;
  font-weight: bolder;
  text-align: center;
  font-size: 16px;
  letter-spacing: 1px;
  color: #638ebf;
}

.TopCellBeforeImage {
  padding-left: 10px;
  padding-right: 5px;
}

.Tabs--ios.Tabs--buttons.Tabs--light .TabsItem--selected {
  background-color: #f1f2f4;
}

.TabsItem--selected .TabsItem__in {
  color: #5181b8;
}

.RegionWrapper {
  align-items: center;
}

.RegionText {
  user-select: none;
  margin-top: 40px;
  font-size: 14pt;
  color: #5d5f61;
  text-align: center;
  font-family: var(--font-common);
}

.Arrow {
  position: relative;
  margin-top: 40px;
  left: 20%;
  width: 30vh;
  height: 48vh;
  z-index: 1;
}

.CheckboxDiv::before {
  position: absolute;
  bottom: 100%;
  left: 0;
  width: 100%;
  height: 0.5px;
  background: #d7d8d9;
  background: var(--separator_common);
  -webkit-transform-origin: center bottom;
  transform-origin: center bottom;
  content: ""
}



.CheckBoxPadding{
  background-color: #ffffff;
  padding-top: 7px;
  padding-bottom: 5px;
  padding-left: 27px;
}

.CellBackground{
  background-color: #ffffff;
}

.CheckBoxText{
  font-family: var(--font-common);
  color: #5d5f61;
}
.Tabbar--ios.Tabbar--shadow::before {
  background: white !important;
}
